.featured {
    height: 100vh;
    position: relative;
    background: #000;

    .category {
        position: absolute;
        top: 80px;
        left: 50px;
        font-size: 20px;
        font-weight: 500;
        color: white;
        display: flex;
        align-items: center;

        select {
            cursor: pointer;
            background-color: transparent;
            border: 1px solid white;
            color: white;
            margin-left: 20px;
            padding: 5px;
        }
    }

    .bg-img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        filter: blur(1px);
    }

    .info {
        width: 35%;
        position: absolute;
        left: 50px;
        bottom: 10vh;
        color: #ddd;
        display: flex;
        flex-direction: column;
        background: #000b;
        padding: 15px;
        line-height: 1.2;
        box-sizing: border-box;
        border-radius: 50px;

        #desc-title{
            font-weight: bolder;
            display: block;
            text-align: center;
            margin-top: -10px;
            margin-bottom: 10px;
            font-size: 20px;
        }

        img {
            width: 22vh;
            align-self: center;
            border-radius: 40px;
        }

        .desc {
            margin: 20px 0px;
            font-size: 15px;
        }

        .buttons {
            display: flex;
            margin: auto;

            button {
                padding: 10px 20px;
                border: none;
                border-radius: 5px;
                display: flex;
                align-items: center;
                justify-content: center;
                font-size: 18px;
                font-weight: 500;
                margin-right: 10px;
                cursor: pointer;

                &.play {
                    background-color: #000;
                    color: #fff;
                }

                &.more {
                    background: rgb(0, 255, 177);
                    background: -moz-linear-gradient(
                        90deg,
                        rgba(0, 255, 177, 1) 36%,
                        rgba(0, 212, 255, 1) 100%
                    );
                    background: -webkit-linear-gradient(
                        90deg,
                        rgba(0, 255, 177, 1) 36%,
                        rgba(0, 212, 255, 1) 100%
                    );
                    background: linear-gradient(
                        90deg,
                        rgba(0, 255, 177, 1) 36%,
                        rgba(0, 212, 255, 1) 100%
                    );
                    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#00ffb1",endColorstr="#00d4ff",GradientType=1);
                    color: white;
                }

                span {
                    margin-left: 5px;
                }
            }
        }
    }
}

@media only screen and (max-width: 768px) {
    .featured .info img{
        display: none !important;
    }

    .featured .info .desc {
        color: #ddd !important;
        margin: 20px 0;
    }

    .featured .info{
        display: grid;
        position: absolute;
        width: 90%;
        margin: 20px;
        left: 0;
        bottom: 50px;
        padding: 20px 10px;
    }
    .desc-title{
        font-weight: bolder;   
        display: block;
        text-align: center;
    }
}